.button {
  background: #4537de;
  border-color: #4537de;
  box-shadow: 0 0.1rem 0.3rem #4537de66;
  color: #fff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  font-variation-settings: "wght" 550;
  font-weight: 400;
  padding: 0.75rem 1.5rem;
  text-align: center;
  margin: 10px auto;
}
